import { all, call, fork, put, takeEvery } from "redux-saga/effects";
import axios from "axios";

import {
  DASHBOARD_GET_PATIENTS_LIST,
  DASHBOARD_GET_INVITATIONS_LIST,
  DASHBOARD_UPDATE_INVITATION
} from "../actions";

import {
  getPatientsListSuccess,
  getPatientsListError,
  getInvitationsListSuccess,
  getInvitationsListError,
  updateInvitationSuccess,
  updateInvitationError
} from "./actions";

export function* watchGetPatientsList() {
  yield takeEvery(DASHBOARD_GET_PATIENTS_LIST, getPatientsList);
}

const getPatientsListAsync = async () => {
  return await axios
    .get("/users/")
    .then(data => data)
    .catch(error => error);
};

function* getPatientsList() {
  try {
    const patientsList = yield call(getPatientsListAsync);

    if (!patientsList.message) {
      yield put(getPatientsListSuccess(patientsList.data));
    } else {
      yield put(getPatientsListError(patientsList.message));
    }
  } catch (error) {
    yield put(getPatientsListError(error));
  }
}

export function* watchGetInvitationsList() {
  yield takeEvery(DASHBOARD_GET_INVITATIONS_LIST, getInvitationsList);
}

const getInvitationsListAsync = async (phone_number, password) => {
  return await axios
    .get("/users/relations/requests/")
    .then(data => data)
    .catch(error => error);
};

function* getInvitationsList() {
  try {
    const invitationsList = yield call(getInvitationsListAsync);

    if (!invitationsList.message) {
      yield put(getInvitationsListSuccess(invitationsList.data));
    } else {
      yield put(getInvitationsListError(invitationsList.message));
    }
  } catch (error) {
    yield put(getInvitationsListError(error));
  }
}

export function* watchUpdateInvitation() {
  yield takeEvery(DASHBOARD_UPDATE_INVITATION, updateInvitation);
}

const updateInvitationAsync = async (relationId, data) => {
  return await axios
    .put(`/users/relations/requests/${relationId}/`, data)
    .then(data => data)
    .catch(error => error);
};

function* updateInvitation({ payload }) {
  const { relationId, data } = payload.invitation;

  try {
    const invitation = yield call(updateInvitationAsync, relationId, data);

    if (!invitation.message) {
      yield put(updateInvitationSuccess(invitation.data));
    } else {
      yield put(updateInvitationError(invitation.message));
    }
  } catch (error) {
    yield put(updateInvitationError(error));
  }
}

export default function* rootSaga() {
  yield all([
    fork(watchGetPatientsList),
    fork(watchGetInvitationsList),
    fork(watchUpdateInvitation)
  ]);
}
