import {
  DASHBOARD_GET_PATIENTS_LIST,
  DASHBOARD_GET_PATIENTS_LIST_SUCCESS,
  DASHBOARD_GET_PATIENTS_LIST_ERROR,
  DASHBOARD_GET_INVITATIONS_LIST,
  DASHBOARD_GET_INVITATIONS_LIST_SUCCESS,
  DASHBOARD_GET_INVITATIONS_LIST_ERROR,
  DASHBOARD_UPDATE_INVITATION,
  DASHBOARD_UPDATE_INVITATION_SUCCESS,
  DASHBOARD_UPDATE_INVITATION_ERROR
} from "../actions";

export const getPatientsList = () => ({
  type: DASHBOARD_GET_PATIENTS_LIST,
  payload: {}
});
export const getPatientsListSuccess = patients => ({
  type: DASHBOARD_GET_PATIENTS_LIST_SUCCESS,
  payload: patients
});
export const getPatientsListError = message => ({
  type: DASHBOARD_GET_PATIENTS_LIST_ERROR,
  payload: { message }
});

export const getInvitationsList = () => ({
  type: DASHBOARD_GET_INVITATIONS_LIST,
  payload: {}
});
export const getInvitationsListSuccess = invitations => ({
  type: DASHBOARD_GET_INVITATIONS_LIST_SUCCESS,
  payload: invitations
});
export const getInvitationsListError = message => ({
  type: DASHBOARD_GET_INVITATIONS_LIST_ERROR,
  payload: { message }
});

export const updateInvitation = () => ({
  type: DASHBOARD_UPDATE_INVITATION,
  payload: {}
});
export const updateInvitationSuccess = invitation => ({
  type: DASHBOARD_UPDATE_INVITATION_SUCCESS,
  payload: invitation
});
export const updateInvitationError = message => ({
  type: DASHBOARD_UPDATE_INVITATION_ERROR,
  payload: { message }
});
