import {
  LOGIN_USER,
  LOGIN_USER_SUCCESS,
  LOGOUT_USER,
  REGISTER_USER,
  REGISTER_USER_SUCCESS,
  LOGIN_USER_ERROR,
  REGISTER_USER_ERROR,
  FORGOT_PASSWORD,
  FORGOT_PASSWORD_SUCCESS,
  FORGOT_PASSWORD_ERROR,
  RESET_PASSWORD,
  RESET_PASSWORD_SUCCESS,
  RESET_PASSWORD_ERROR,
  VERIFY_PHONE_NUMBER,
  VERIFY_PHONE_NUMBER_SUCCESS,
  VERIFY_PHONE_NUMBER_ERROR,
  GET_PROFILE,
  GET_PROFILE_SUCCESS,
  GET_PROFILE_ERROR,
  UPDATE_PROFILE,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_ERROR
} from "../actions";

export const loginUser = (user, history) => ({
  type: LOGIN_USER,
  payload: { user, history }
});
export const loginUserSuccess = user => ({
  type: LOGIN_USER_SUCCESS,
  payload: user
});
export const loginUserError = message => ({
  type: LOGIN_USER_ERROR,
  payload: { message }
});

export const forgotPassword = (forgotUserMail, history) => ({
  type: FORGOT_PASSWORD,
  payload: { forgotUserMail, history }
});
export const forgotPasswordSuccess = forgotUserMail => ({
  type: FORGOT_PASSWORD_SUCCESS,
  payload: forgotUserMail
});
export const forgotPasswordError = message => ({
  type: FORGOT_PASSWORD_ERROR,
  payload: { message }
});

export const resetPassword = ({ resetPasswordCode, newPassword, history }) => ({
  type: RESET_PASSWORD,
  payload: { resetPasswordCode, newPassword, history }
});
export const resetPasswordSuccess = newPassword => ({
  type: RESET_PASSWORD_SUCCESS,
  payload: newPassword
});
export const resetPasswordError = message => ({
  type: RESET_PASSWORD_ERROR,
  payload: { message }
});

export const registerUser = (user, history) => ({
  type: REGISTER_USER,
  payload: { user, history }
});
export const registerUserSuccess = user => ({
  type: REGISTER_USER_SUCCESS,
  payload: user
});
export const registerUserError = message => ({
  type: REGISTER_USER_ERROR,
  payload: { message }
});

export const verifyPhoneNumber = phone_number => ({
  type: VERIFY_PHONE_NUMBER,
  payload: { phone_number }
});

export const verifyPhoneNumberSuccess = phone_number => {
  return {
    type: VERIFY_PHONE_NUMBER_SUCCESS,
    payload: { phone_number }
  };
};

export const verifyPhoneNumberError = message => ({
  type: VERIFY_PHONE_NUMBER_ERROR,
  payload: { message }
});

export const getProfile = profile => ({
  type: GET_PROFILE,
  payload: profile
});

export const getProfileSuccess = profile => {
  return {
    type: GET_PROFILE_SUCCESS,
    payload: profile
  };
};

export const getProfileError = message => ({
  type: GET_PROFILE_ERROR,
  payload: { message }
});

export const updateProfile = (profile, history) => ({
  type: UPDATE_PROFILE,
  payload: { profile, history }
});

export const updateProfileSuccess = profile => {
  return {
    type: UPDATE_PROFILE_SUCCESS,
    payload: profile
  };
};

export const updateProfileError = message => ({
  type: UPDATE_PROFILE_ERROR,
  payload: { message }
});

export const logoutUser = history => ({
  type: LOGOUT_USER,
  payload: { history }
});
