import { all, call, fork, put, takeEvery } from "redux-saga/effects";
import axios from "axios";

import { auth } from "../../helpers/Firebase";
import {
  LOGIN_USER,
  REGISTER_USER,
  LOGOUT_USER,
  FORGOT_PASSWORD,
  RESET_PASSWORD,
  VERIFY_PHONE_NUMBER,
  GET_PROFILE,
  UPDATE_PROFILE
} from "../actions";

import {
  loginUserSuccess,
  loginUserError,
  registerUserSuccess,
  registerUserError,
  forgotPasswordSuccess,
  forgotPasswordError,
  resetPasswordSuccess,
  resetPasswordError,
  verifyPhoneNumberSuccess,
  verifyPhoneNumberError,
  getProfileSuccess,
  getProfileError,
  updateProfileSuccess,
  updateProfileError
} from "./actions";

export function* watchLoginUser() {
  yield takeEvery(LOGIN_USER, loginWithEmailPassword);
}

const loginWithEmailPasswordAsync = async (phone_number, password) => {
  return await axios
    .post("/auth/login/", {
      username: phone_number,
      password
    })
    .then(data => data)
    .catch(error => error);
};

function* loginWithEmailPassword({ payload }) {
  const { phone_number, password } = payload.user;
  const { history } = payload;

  try {
    const loginUser = yield call(
      loginWithEmailPasswordAsync,
      phone_number,
      password
    );

    if (!loginUser.message) {
      yield put(loginUserSuccess(loginUser.data));

      const { token } = loginUser.data;

      if (token && typeof token !== "undefined") {
        localStorage.setItem("token", token);

        axios.defaults.headers.common["Authorization"] = `Token ${token}`;

        history.push("/app/patients-list");
      }
    } else {
      yield put(loginUserError(loginUser.message));
    }
  } catch (error) {
    yield put(loginUserError(error));
  }
}

export function* watchRegisterUser() {
  yield takeEvery(REGISTER_USER, registerWithPhoneNumberPassword);
}

const registerWithPhoneNumberPasswordAsync = async (phone_number, code) => {
  return await axios.post("/auth/token/", {
    phone_number,
    code
  });
};

function* registerWithPhoneNumberPassword({ payload }) {
  const { phone_number, code } = payload.user;
  const { history } = payload;

  try {
    const registerUser = yield call(
      registerWithPhoneNumberPasswordAsync,
      phone_number,
      code
    );

    if (!registerUser.message) {
      yield put(registerUserSuccess(registerUser.data));

      const { code } = registerUser.data;

      if (code && typeof code !== "undefined") {
        localStorage.setItem("token", code);

        axios.defaults.headers.common["Authorization"] = `Token ${code}`;

        history.push("/user/create-profile");
      }
    } else {
      yield put(registerUserError(registerUser.message));
    }
  } catch (error) {
    yield put(registerUserError(error));
  }
}

export function* watchLogoutUser() {
  yield takeEvery(LOGOUT_USER, logout);
}

const logoutAsync = async history => {
  await axios
    .get(`/auth/logout/`)
    .then(data => data)
    .catch(error => error);
};

function* logout({ payload }) {
  const { history } = payload;

  try {
    yield call(logoutAsync, history);

    localStorage.removeItem("token");

    history.push("/user/login");
  } catch (error) {}
}

export function* watchForgotPassword() {
  yield takeEvery(FORGOT_PASSWORD, forgotPassword);
}

const forgotPasswordAsync = async email => {
  return await auth
    .sendPasswordResetEmail(email)
    .then(user => user)
    .catch(error => error);
};

function* forgotPassword({ payload }) {
  const { email } = payload.forgotUserMail;
  try {
    const forgotPasswordStatus = yield call(forgotPasswordAsync, email);
    if (!forgotPasswordStatus) {
      yield put(forgotPasswordSuccess("success"));
    } else {
      yield put(forgotPasswordError(forgotPasswordStatus.message));
    }
  } catch (error) {
    yield put(forgotPasswordError(error));
  }
}

export function* watchResetPassword() {
  yield takeEvery(RESET_PASSWORD, resetPassword);
}

const resetPasswordAsync = async (resetPasswordCode, newPassword) => {
  return await auth
    .confirmPasswordReset(resetPasswordCode, newPassword)
    .then(user => user)
    .catch(error => error);
};

function* resetPassword({ payload }) {
  const { newPassword, resetPasswordCode } = payload;

  try {
    const resetPasswordStatus = yield call(
      resetPasswordAsync,
      resetPasswordCode,
      newPassword
    );
    if (!resetPasswordStatus) {
      yield put(resetPasswordSuccess("success"));
    } else {
      yield put(resetPasswordError(resetPasswordStatus.message));
    }
  } catch (error) {
    yield put(resetPasswordError(error));
  }
}

export function* watchVerifyPhoneNumber() {
  yield takeEvery(VERIFY_PHONE_NUMBER, verifyPhoneNumber);
}

const verifyPhoneNumberAsync = async phone_number => {
  return await axios
    .get(`/auth/token/?phone_number=${encodeURIComponent(phone_number)}`)
    .then(data => data)
    .catch(error => error);
};

function* verifyPhoneNumber({ payload }) {
  const { phone_number } = payload;

  try {
    const phoneNumberCall = yield call(verifyPhoneNumberAsync, phone_number);

    if (!phoneNumberCall.message) {
      yield put(verifyPhoneNumberSuccess(phone_number));
    } else {
      yield put(verifyPhoneNumberError(phoneNumberCall.message));
    }
  } catch (error) {
    yield put(verifyPhoneNumberError(error));
  }
}

export function* watchGetProfile() {
  yield takeEvery(GET_PROFILE, getProfile);
}

const getProfileAsync = async () => {
  return await axios
    .get(`/users/me/`)
    .then(data => data)
    .catch(error => error);
};

function* getProfile() {
  try {
    const profileCall = yield call(getProfileAsync);

    if (!profileCall.message) {
      yield put(getProfileSuccess(profileCall.data));
    } else {
      yield put(getProfileError(profileCall.message));
    }
  } catch (error) {
    yield put(getProfileError(error));
  }
}

export function* watchUpdateProfile() {
  yield takeEvery(UPDATE_PROFILE, updateProfile);
}

const updateProfileAsync = async profile => {
  return await axios
    .patch(`/users/me/`, profile)
    .then(data => data)
    .catch(error => error);
};

function* updateProfile({ payload }) {
  try {
    const { profile, history } = payload;
    const profileData = {};

    for (let [key, value] of Object.entries(profile)) {
      if (value !== "") {
        profileData[key] = value;
      }
    }

    const profileCall = yield call(updateProfileAsync, profileData);

    if (!profileCall.message) {
      yield put(updateProfileSuccess(profileCall.data));

      history.push("/app/patients-list");
    } else {
      yield put(updateProfileError(profileCall.message));
    }
  } catch (error) {
    yield put(updateProfileError(error));
  }
}

export default function* rootSaga() {
  yield all([
    fork(watchLoginUser),
    fork(watchLogoutUser),
    fork(watchRegisterUser),
    fork(watchForgotPassword),
    fork(watchResetPassword),
    fork(watchVerifyPhoneNumber),
    fork(watchGetProfile),
    fork(watchUpdateProfile)
  ]);
}
