import {
  DASHBOARD_GET_PATIENTS_LIST,
  DASHBOARD_GET_PATIENTS_LIST_SUCCESS,
  DASHBOARD_GET_PATIENTS_LIST_ERROR,
  DASHBOARD_GET_INVITATIONS_LIST,
  DASHBOARD_GET_INVITATIONS_LIST_SUCCESS,
  DASHBOARD_GET_INVITATIONS_LIST_ERROR,
  DASHBOARD_UPDATE_INVITATION,
  DASHBOARD_UPDATE_INVITATION_SUCCESS,
  DASHBOARD_UPDATE_INVITATION_ERROR
} from "../actions";

const INIT_STATE = {
  patients: { results: [] },
  invitations: { results: [] },
  invitation: {}
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case DASHBOARD_GET_PATIENTS_LIST:
      return { ...state, payload: action.payload, error: "" };
    case DASHBOARD_GET_PATIENTS_LIST_SUCCESS:
      return { ...state, patients: action.payload, error: "" };
    case DASHBOARD_GET_PATIENTS_LIST_ERROR:
      return { ...state, error: action.payload.message };
    case DASHBOARD_GET_INVITATIONS_LIST:
      return { ...state, payload: action.payload, error: "" };
    case DASHBOARD_GET_INVITATIONS_LIST_SUCCESS:
      return { ...state, invitations: action.payload, error: "" };
    case DASHBOARD_GET_INVITATIONS_LIST_ERROR:
      return { ...state, error: action.payload.message };
    case DASHBOARD_UPDATE_INVITATION:
      return { ...state, payload: action.payload, error: "" };
    case DASHBOARD_UPDATE_INVITATION_SUCCESS:
      return { ...state, invitation: action.payload, error: "" };
    case DASHBOARD_UPDATE_INVITATION_ERROR:
      return { ...state, error: action.payload.message };
    default:
      return { ...state };
  }
};
